import React from "react";
import { Row, Col, Card, CardBody, CardTitle } from "reactstrap";
import CardComp from "../../../components/card-component/card-component";
import Balance from "./your-card-form";

class YourCard extends React.Component {
  state = {
    cardStatus: false,
    user: {
      nCard: "",
      valCard: ""
      // name: "MAURO C. C. VIEIRA"
    }
  };

  handleComponentChanges = input => e => {
    const { user } = this.state;
    user[input] = e.target.value;
    this.setState({ user });
  };

  handleCardChanges = e => {
    this.setState({ cardStatus: e });
  };

  render() {
    const { cardStatus, user } = this.state;
    return (
      <Card id="yourCardComponent">
        <CardBody>
          <CardTitle className="section-card-title">
            <i className="icon-wallet" /> Seu cartão
          </CardTitle>
          <Row className="">
            <Col xs="12" md="6" className="">
              <Balance
                onChange={this.handleComponentChanges}
                user={user}
                cardStatus={cardStatus}
                onChangeCardStatus={this.handleCardChanges}
              />
            </Col>
            <Col xs="12" md="6" className="">
              <CardComp user={user} />
            </Col>
          </Row>
        </CardBody>
      </Card>
    );
  }
}

export default YourCard;



