import React, { Component } from "react";
import { Button, Row, Col, Form, Container } from "reactstrap";

export class StepZero extends Component {
  constructor(props) {
    super(props);
  }

  // Testa a validade das informações e avança para próxima etapa
  continue = e => {
    e.preventDefault();
    this.props.nextStep();
  };

  render() {
    const { values } = this.props;
    return (
      <Container fluid className="self-registerIntroduction h-100">
        <Row className="justify-content-center my-5">
          <Col xs="12" className="text-center ">
            <h1 className="title">Falta pouco!</h1>
          </Col>
        </Row>
        <Row className="justify-content-center my-5">
          <Col xs="12" className="description text-center">
            É só preencher os dados complementares do cadastro e aproveitar o
            Banky
          </Col>
        </Row>
        <Row className="justify-content-center my-5">
          <Col className="col-12 col-sm-auto mx-auto">
            <Button
              size="lg"
              block
              type="submit"
              onClick={this.continue}
              className="btn btn-light text-primary"
            >
              Continuar
            </Button>
          </Col>
        </Row>
      </Container>
    );
  }
}

export default StepZero;



