import React, { Component } from "react";
import { Button, Row, Col, Form, Container } from "reactstrap";
import Validator from "./validator";
import InputBanky from "../../components/input-banky/input-banky";
export class StepOne extends Component {
  constructor(props) {
    super(props);
    this.validators = Validator;
    this.state = this.props.values;
  }

  // Testa a validade das informações e avança para próxima etapa
  continue = e => {
    e.preventDefault();
    this.setState(this.props.values);
    let res = this.validForm();
    // let res = true;
    if (res) this.props.nextStep();
  };

  validForm() {
    let status = true;
    Object.keys(this.state).forEach(field => {
      this.formValidators(field, this.props.values[field]);

      if (!this.validators[field].valid) {
        status = false;
      }
    });
    return status;
  }

  formValidators(fieldName, value) {
    this.validators[fieldName].errors = [];
    this.validators[fieldName].state = value;
    this.validators[fieldName].valid = true;
    this.validators[fieldName].rules.forEach(rule => {
      
      if (rule.test instanceof RegExp) {       
        if (!rule.test.test(value)) {
          this.validators[fieldName].errors.push(rule.message);
          this.validators[fieldName].valid = false;
        }
      } else if (typeof rule.test === "function") {
        if (!rule.test(value)) {
          this.validators[fieldName].errors.push(rule.message);
          this.validators[fieldName].valid = false;
        }
      }
    });
    this.setState(this.state);
  }

  showErrors(fieldName) {
    if (fieldName === "") {
      return <div className="error" />;
    }
    const validator = this.validators[fieldName];

    const result = "";

    if (validator && !validator.valid) {
      const errors = validator.errors.map((info, index) => {
        return (
          <span className="error" key={index}>
            <i className="mdi mdi-alert-octagram" /> {info}
          </span>
        );
      });
      return <div className="error">{errors}</div>;
    } else {
      return <div className="error" />;
    }
  }
  handleComponentChanges = input => e => {
    let value = e.target.value;   
    this.setState({ [input]: value });
    this.props.handleChanges(input, value);
  };
  render() {
    const { values } = this.props;
    return (
      <Container fluid className="steps h-100 min-height-700">
        {/*      */}
        {/* Form */}
        {/*      */}
        <Form className="form-material mb-auto">
          <Row className="justify-content-center align-content-top">
            <Col xs="12" className="mx-auto">
              <InputBanky
                label="CNPJ"
                type="text"
                onChange={this.handleComponentChanges("cnpj")}
                value={values.cnpj}
                name="cnpj"
                mask={this.validators["cnpj"].mask}
                formNoValidate
                required
              />
              {this.showErrors("cnpj")}
            </Col>

            <Col xs="12" className="mx-auto">
              <InputBanky
                label="Nome fantasia"
                onChange={this.handleComponentChanges("nomeFantasia")}
                type="text"
                value={values.nomeFantasia}
                name="nomeFantasia"
                mask={this.validators["nomeFantasia"].mask}
                formNoValidate
                required
              />
              {this.showErrors("nomeFantasia")}
            </Col>

            <Col xs="12" className="mx-auto">
              <InputBanky
                label="Telefone da empresa"
                onChange={this.handleComponentChanges("telefoneEmpresa")}
                type="text"
                value={values.telefoneEmpresa}
                name="telefoneEmpresa"
                mask={this.validators["telefoneEmpresa"].mask}
                formNoValidate
                required
              />
              {this.showErrors("telefoneEmpresa")}
            </Col>

            <Col xs="12" className="mx-auto">
              <InputBanky
                label="Nome do responsável"
                onChange={this.handleComponentChanges("nomeResponsavel")}
                type="text"
                value={values.nomeResponsavel}
                mask={this.validators["nomeResponsavel"].mask}
                name="nomeResponsavel"
                formNoValidate
                required
              />
              {this.showErrors("nomeResponsavel")}
            </Col>

            <Col xs="12" className="mx-auto">
              <InputBanky
                label="CPF do responsável"
                onChange={this.handleComponentChanges("cpfResponsavel")}
                type="text"
                value={values.cpfResponsavel}
                mask={this.validators["cpfResponsavel"].mask}
                name="cpfResponsavel"
                formNoValidate
                required
              />
              {this.showErrors("cpfResponsavel")}
            </Col>

            <Col xs="12" className="mx-auto">
              <Button
                size="lg"
                block
                type="submit"
                onClick={this.continue}
                className="btn btn-light text-primary"
              >
                Continuar
              </Button>
            </Col>
          </Row>
        </Form>
      </Container>
    );
  }
}

export default StepOne;



