import React, { Component } from "react";
import { Input, Label, FormGroup } from "reactstrap";
import InputMask from 'react-input-mask';
export default class InputBanky extends Component {
  state = {
    isFocused: false,
    size: "xs"
  };
  constructor(props) {
    super(props);

    this.textInput = React.createRef();
    this.focusTextInput = this.focusTextInput.bind(this);
    this.updateWindowDimensions = this.updateWindowDimensions.bind(this);
  }
  handlerFocus = () => this.setState({ isFocused: true });
  handlerBlur = () => {
    this.setState({ isFocused: false });
  };
  focusTextInput() {
    this.textInput.current.getInputDOMNode().focus();
  }
  componentDidMount() {
    this.updateWindowDimensions();
    window.addEventListener("resize", this.updateWindowDimensions);
  }

  componentWillUnmount() {
    window.removeEventListener("resize", this.updateWindowDimensions);
  }

  updateWindowDimensions() {
    let w = {
      size:
        window.innerWidth < 576
          ? "xs"
          : window.innerWidth < 768
          ? "sm"
          : window.innerWidth < 992
          ? "md"
          : window.innerWidth < 1200
          ? "lg"
          : "xl"
    };
    this.setState(w);
  }
  render() {
    const { label, validation, mask, ...props } = this.props;
    const { isFocused } = this.state;

    //------------------------------------------------------------------
    // Comportamento padrão
    //------------------------------------------------------------------
    let labelStyle = {
      position: "absolute",
      left: 0,
      bottom: !this.props.value && !isFocused ? "0" : "24px",
      fontSize: !this.props.value && !isFocused ? "14px" : "16px",
      lineHeight: !this.props.value && !isFocused ? "25px" : "29px",
      opacity: !this.props.value && !isFocused ? "1" : "0.8",
      color: "#fff",
      margin: 0,      
      fontFamily: "Karla",
      transition: "all 0.35s ease-in-out"
    };
    let inputStyle = {
      fontFamily: "Karla",
      color: "#fff",
      // background: "red",
      fontSize: "16px",
      lineHeight: "20px",
      fontWeight: "bold"
    };
    let formGroupStyle = {
      paddingTop: "26px",
      marginBottom: 0
    };

    //------------------------------------------------------------------
    // Comportamento responsivo
    //------------------------------------------------------------------
    if (
      this.state.size === "xs" ||
      this.state.size === "sm" ||
      this.state.size === "md"
    ) {
      labelStyle = {
        position: "absolute",
        left: 0,
        bottom: !this.props.value && !isFocused ? "2px" : "24px",
        fontSize: !this.props.value && !isFocused ? "14px" : "16px",
        lineHeight: !this.props.value && !isFocused ? "17px" : "19px",
        color: "#fff",
        margin: 0,
        fontFamily: "Karla",
        transition: "all 0.35s ease-in-out"
      };
      inputStyle = {
        fontFamily: "Karla",
        color: "#fff",
        fontSize: "16px",
        lineHeight: "19px",
        fontWeight: "bold"
      };
      formGroupStyle = {
        paddingTop: "6px",
        marginBottom: 0
      };
    }

    return (
      <FormGroup style={formGroupStyle}>
        <Label style={labelStyle} onClick={this.focusTextInput}>
          {label}
        </Label>
        <Input
          innerRef={this.textInput}
          style={inputStyle}
          onFocus={this.handlerFocus}
          onBlur={this.handlerBlur}
          maskChar={null}
          mask={mask}
          tag={InputMask}
          {...props}
        />
      </FormGroup>
    );
  }
}



