import React, { Component } from "react";
import { Button, Row, Col, Container } from "reactstrap";
import img from "../../assets/images/logo-white.svg";
import Validator from "./validator";
import sucesso from "../../assets/images/icon/sucesso.svg";

export class queueDone extends Component {
  constructor(props) {
    super(props);
    this.validators = Validator;
    this.nextStep = this.props.nextStep;
  }

  handleClick = e => {
    e.preventDefault();
    this.nextStep();
  };

  render() {
    return (
      <Container className="QueueDone">
        <img src={sucesso} alt="" className="img-fluid" srcSet="" />

        <div className="text-center description">
          <div className="text-center title">Parabéns!</div>
          <div className="description-text">
            Você está em nossa lista de espera Banky, e em breve entraremos em
            contato.
          </div>
          <Button
            size="lg"
            block
            type="submit"
            onClick={this.handleClick}
            className="btn btn-outline-light"
          >
            Entrar na lista de espera
          </Button>
        </div>
      </Container>
    );
  }
}

export default queueDone;



