import React, { Component } from "react";
import { Button, Row, Col, Card, CardBody, CardTitle, Form } from "reactstrap";
import InputBanky from "../../../components/input-banky/input-banky";
import Validator from "./validator";

export class YourCardForm extends Component {
  state = {
    step: 0,
    user: {
      nCard: "",
      valCard: ""
      // name: "MAURO C. C. VIEIRA"
    }
  };

  constructor(props) {
    const { user } = props;
    super(props);
    this.validators = Validator;
    this.user = user;
  }

  handleComponentChanges = input => e => {
    const { onChange } = this.props;
    const { user } = this.state;
    user[input] = e.target.value;
    this.setState({ user });
    onChange(input)(e);
  };

  activeCard = () => {
    const { step } = this.state;
    this.setState({ step: step + 1 });
  };

  cardIsInactive = () => {
    const { step } = this.state;
    switch (step) {
      case 0:
        return this.cardIsInactiveStep0(step);
      case 1:
        return this.cardIsInactiveStep1(step);
      default:
        return this.cardIsInactiveStep0(step);
    }
  };

  cardIsInactiveStep0 = step => (
    <CardBody key={step} className="not-received-card">
      <CardTitle className="section-card-title">
        <i className="" />
        Você recebeu seu cartão?
      </CardTitle>
      <Row className="align-items-center">
        <Col xs="12" className="mb-2 text-justify">
          Para desbloquear seu cartão é simples. Basta clicar no botão abaixo,
          preencher as informações solicitados e pronto. Seu cartão está pronto
          para ser utilizado.
        </Col>
        <Col xs="12" className="mt-2 text-center">
          <Button color="primary" onClick={this.activeCard}>
            Quero ativar meu cartão
          </Button>
        </Col>
      </Row>
    </CardBody>
  );

  cardIsInactiveStep1 = step => {
    const {
      user: { nCard, valCard }
    } = this.state;
    return (
      <CardBody key={step} className="not-received-card hasForm">
        <CardTitle className="section-card-title">Ativar Cartão</CardTitle>
        <Row className="align-items-center">
          <Col xs="12" className="mb-2 text-justify">
            Digite as informações abaixo para realizar a ativação do seu cartão
            Banky
          </Col>
          <Col xs="12" className="">
            {/*      */}
            {/* Form */}
            {/*      */}
            <Form className="form-material mb-auto">
              <Row className="justify-content-center align-content-top">
                <Col xs="12" className="mx-auto">
                  <InputBanky
                    label="Número do Cartão"
                    type="text"
                    onChange={this.handleComponentChanges("nCard")}
                    value={nCard}
                    name="nCard"
                    mask={this.validators.nCard.mask}
                    formNoValidate
                    required
                  />
                  {this.showErrors("nCard")}
                </Col>
              </Row>
              <Row className="justify-content-center align-content-top">
                <Col xs="12" className="mx-auto">
                  <InputBanky
                    label="Validade (MM/AAAA)"
                    type="text"
                    onChange={this.handleComponentChanges("valCard")}
                    value={valCard}
                    name="valCard"
                    mask={this.validators.valCard.mask}
                    formNoValidate
                    required
                  />
                  {this.showErrors("valCard")}
                </Col>
              </Row>
            </Form>
          </Col>
          <Col xs="12" className="mt-2">
            <Button color="primary" onClick={this.validForm}>
              Quero ativar meu cartão
            </Button>
          </Col>
        </Row>
      </CardBody>
    );
  };

  cardIsActive = () => (
    <Row noGutters className="justify-content-around align-items-start">
      <Col xs="auto" className="text-center">
        <Card className="balance-card">
          <CardBody>
            <Row className="align-items-center">
              <Col xs="12" className="mb-2">
                <span>Saldo atualizado as 10:45</span>
              </Col>
              <Col xs="12" className="my-2">
                <span className="display-6 text-success my-auto">R$ 35,28</span>
              </Col>
              <Col xs="12" className="mt-2">
                <span className="text-muted">Gasto no mês: R$ 300,45</span>
              </Col>
            </Row>
          </CardBody>
        </Card>
      </Col>
    </Row>
  );

  validForm = () => {
    let status = true;
    const { onChangeCardStatus } = this.props;
    Object.keys(this.validators).forEach(field => {
      const {
        user: { [field]: currentState }
      } = this.state;
      this.formValidators(field, currentState);
      if (!this.validators[field].valid) {
        status = false;
      }
    });
    if (status) onChangeCardStatus(status);
  };

  formValidators(fieldName, value) {
    this.validators[fieldName].errors = [];
    this.validators[fieldName].state = value;
    this.validators[fieldName].valid = true;
    this.validators[fieldName].rules.forEach(rule => {
      if (rule.test instanceof RegExp) {
        if (!rule.test.test(value)) {
          this.validators[fieldName].errors.push(rule.message);
          this.validators[fieldName].valid = false;
        }
      } else if (typeof rule.test === "function") {
        if (!rule.test(value)) {
          this.validators[fieldName].errors.push(rule.message);
          this.validators[fieldName].valid = false;
        }
      }
    });
    this.setState(st => st);
  }

  showErrors(fieldName) {
    if (fieldName === "") {
      return <div className="error" />;
    }
    const validator = this.validators[fieldName];

    if (validator && !validator.valid) {
      const errors = validator.errors.map((info, index) => (
        // eslint-disable-next-line react/no-array-index-key
        <span className="error" key={index}>
          <i className="mdi mdi-alert-octagram" /> {info}
        </span>
      ));
      return <div className="error">{errors}</div>;
    }
    return <div className="error" />;
  }

  render() {
    const { cardStatus } = this.props;
    return cardStatus ? this.cardIsActive() : this.cardIsInactive();
  }
}

export default YourCardForm;



