import React from "react";
import { Row, Col } from "reactstrap";
import Balance from "./your-card/your-card";

class Home extends React.Component {
  state = {
    hasCard: true
  };

  activeCard() {
    this.setState({ hasCard: true });
  }

  render() {
    const { hasCard } = this.state;
    return (
      <div className="home-component ">
        {hasCard}
        <Row>
          <Col>
            <Balance />
          </Col>
        </Row>
      </div>
    );
  }
}

export default Home;



