import React from "react";
import { Route, Switch, Redirect } from "react-router-dom";
import Header from "../components/header/header";
import Sidebar from "../components/sidebar/sidebar";
import Footer from "../components/footer/footer";
import ThemeRoutes from "../routes/routing";

class Fulllayout extends React.Component {
  /*--------------------------------------------------------------------------------*/
  /* Change the layout settings [HEADER,SIDEBAR && DARK LAYOUT] from here            */
  /*--------------------------------------------------------------------------------*/
  constructor(props) {
    super(props);
    const { history } = this.props;
    this.updateDimensions = this.updateDimensions.bind(this);
    this.state = {
      width: window.innerWidth,
      settings: [
        {
          theme: "light",
          layout: "vertical",
          dir: "ltr",
          sidebartype: "full",
          sidebarpos: "fixed",
          headerpos: "fixed",
          boxed: "full",
          navbarbg: "skin4",
          sidebarbg: "skin6",
          logobg: "skin4"
        }
      ]
    };

    history.listen(() => {
      if (
        window.innerWidth < 767 &&
        document
          .getElementById("main-wrapper")
          .className.indexOf("show-sidebar") !== -1
      ) {
        document
          .getElementById("main-wrapper")
          .classList.toggle("show-sidebar");
      }
    });
  }

  /*--------------------------------------------------------------------------------*/
  /* Life Cycle Hook, Applies when loading or resizing App                           */
  /*--------------------------------------------------------------------------------*/
  componentDidMount() {
    window.addEventListener("load", this.updateDimensions);
    window.addEventListener("resize", this.updateDimensions);
  }

  /*--------------------------------------------------------------------------------*/
  /* Life Cycle Hook                                                                 */
  /*--------------------------------------------------------------------------------*/
  componentWillUnmount() {
    window.removeEventListener("load", this.updateDimensions);
    window.removeEventListener("resize", this.updateDimensions);
  }

  /*--------------------------------------------------------------------------------*/
  /* Function that handles sidebar, changes when resizing App                        */
  /*--------------------------------------------------------------------------------*/
  updateDimensions() {
    const { width, settings } = this.state;
    const element = document.getElementById("main-wrapper");
    this.setState({
      width: window.innerWidth
    });
    switch (settings[0].sidebartype) {
      case "full":
      case "iconbar":
        if (width < 1170) {
          element.setAttribute("data-sidebartype", "mini-sidebar");
          element.classList.add("mini-sidebar");
        } else {
          element.setAttribute("data-sidebartype", settings[0].sidebartype);
          element.classList.remove("mini-sidebar");
        }
        break;

      case "overlay":
        if (width < 767) {
          element.setAttribute("data-sidebartype", "mini-sidebar");
        } else {
          element.setAttribute("data-sidebartype", settings[0].sidebartype);
        }
        break;

      default:
    }
  }

  render() {
    /*--------------------------------------------------------------------------------*/
    /* Theme Setting && Layout Options wiil be Change From Here                       */
    /*--------------------------------------------------------------------------------*/
    const { settings } = this.state;
    return (
      <div
        id="main-wrapper"
        dir={settings[0].dir}
        data-theme={settings[0].theme}
        data-layout={settings[0].layout}
        data-sidebartype={settings[0].sidebartype}
        data-sidebar-position={settings[0].sidebarpos}
        data-header-position={settings[0].headerpos}
        data-boxed-layout={settings[0].boxed}
      >
        {/*--------------------------------------------------------------------------------*/}
        {/* Header                                                                         */}
        {/*--------------------------------------------------------------------------------*/}
        <Header data={this.state} />
        {/*--------------------------------------------------------------------------------*/}
        {/* Sidebar                                                                        */}
        {/*--------------------------------------------------------------------------------*/}
        <Sidebar data={this.state} {...this.props} routes={ThemeRoutes} />
        {/*--------------------------------------------------------------------------------*/}
        {/* Page Main-Content                                                              */}
        {/*--------------------------------------------------------------------------------*/}
        <div className="page-wrapper d-block">
          <div className="page-content container-fluid">
            <Switch>
              {ThemeRoutes.map(prop => {
                if (prop.navlabel) {
                  return null;
                }
                if (prop.collapse) {
                  return prop.child.map(prop2 => {
                    if (prop2.collapse) {
                      return prop2.subchild.map(prop3 => (
                        <Route
                          path={prop3.path}
                          component={prop3.component}
                          key={prop3.component}
                        />
                      ));
                    }
                    return (
                      <Route
                        path={prop2.path}
                        component={prop2.component}
                        key={prop2.path}
                      />
                    );
                  });
                }
                if (prop.redirect) {
                  return (
                    <Redirect
                      from={prop.path}
                      to={prop.pathTo}
                      key={prop.path}
                    />
                  );
                }
                return (
                  <Route
                    path={prop.path}
                    component={prop.component}
                    key={prop.path}
                  />
                );
              })}
            </Switch>
          </div>
          <Footer />
        </div>
      </div>
    );
  }
}
export default Fulllayout;



