import React from "react";
import {
  Row,
  Col,
  Card,
  CardBody,
  CardTitle,
  Button,
  Form,
  FormGroup,
  Input
} from "reactstrap";
import { CardComponent } from "../../components/card-component/card-component";

const cardStyle = {
  lineHeight: "0"
};
class Change extends React.Component {
  render() {
    const user = {
      nCard: "5111 1111 1111 1111",
      valCard: "01/2020"
    };
    return (
      <div>
        {/*--------------------------------------------------------------------------------*/}
        {/* 1st Row                                                                        */}
        {/*--------------------------------------------------------------------------------*/}
        <Row noGutters className="">
          <Col xs="12" md="auto">
            <Card>
              <CardBody>
                <Row className="justify-content-around">
                  {/* Título */}
                  <Col xs="12">
                    <div className="d-flex flex-wrap">
                      <div>
                        <CardTitle className="section-card-title">
                          <i className="ti-key" /> Seu cartão
                        </CardTitle>
                      </div>
                    </div>
                  </Col>
                  {/* Imagem cartão */}
                  <Col xs="auto" className="text-center mb-3">
                    <CardComponent user={user} />
                  </Col>
                  {/* Bloquear */}
                  <Col xs="12" sm="auto" className="text-center">
                    <Card className="p-3">
                      <CardBody>
                        <CardTitle className="mb-0 text-left border-bottom pb-1">
                          Cansou da senha atual?
                        </CardTitle>
                        <div>
                          <div className="text-muted my-3 text-justify">
                            Digite abaixo sua nova senha do cartão
                          </div>
                          <Form>
                            <FormGroup>
                              <Input
                                type="password"
                                placeholder="Digite a senha antiga"
                                required
                              />
                            </FormGroup>
                            <FormGroup>
                              <Input
                                type="password"
                                placeholder="Digite a senha nova"
                                required
                              />
                            </FormGroup>
                            <FormGroup>
                              <Input
                                type="password"
                                placeholder="Confirme a senha nova"
                                required
                              />
                            </FormGroup>
                            <Button type="submit" className="btn btn-info">
                              Trocar Senha
                            </Button>
                          </Form>
                        </div>
                      </CardBody>
                    </Card>
                  </Col>
                </Row>
              </CardBody>
            </Card>
          </Col>
        </Row>
      </div>
    );
  }
}

export default Change;



