const validator = {
  nCard: {
    rules: [
      {
        test: /^5[1-4]{3}[ -]?[1-9]{4}[ -]?[1-9]{4}[ -]?[1-9]{4}$/,
        message: "Número inválido."
      }
    ],
    errors: [],
    valid: false,
    state: "",
    mask: "9999-9999-9999-9999"
  },
  valCard: {
    rules: [
      {
        test: /^[0-9]{2}\/?[0-9]{4}/,
        message: "Data de vencimento inválida."
      }
    ],
    errors: [],
    valid: false,
    state: "",
    mask: "99/9999"
  }
};

export default validator;



