import React from "react";
import PropTypes from "prop-types";
import { Nav, NavItem, NavLink, Navbar, Collapse } from "reactstrap";

/*--------------------------------------------------------------------------------*/
/* Import images which are need for the HEADER                                    */
/*--------------------------------------------------------------------------------*/

import logolighttext from "../../assets/images/logo-text-white.svg";

const lightTextStyles = {
  height: 55,
  cursor: "pointer"
};

class Header extends React.Component {
  constructor(props) {
    super(props);
    this.toggle = this.toggle.bind(this);
    this.showMobilemenu = this.showMobilemenu.bind(this);
    this.sidebarHandler = this.sidebarHandler.bind(this);
    this.state = {
      isOpen: false
    };
    this.toggleMenu = this.toggleMenu.bind(this);
  }

  /*--------------------------------------------------------------------------------*/
  /* To open NAVBAR in MOBILE VIEW                                                   */
  /*--------------------------------------------------------------------------------*/
  toggle = () => {
    this.setState(prevState => {
      this.setState({ isOpen: !prevState.isOpen });
    });
  };

  /*--------------------------------------------------------------------------------*/
  /* To open SIDEBAR-MENU in MOBILE VIEW                                             */
  /*--------------------------------------------------------------------------------*/
  showMobilemenu = () => {
    document.getElementById("main-wrapper").classList.toggle("show-sidebar");
  };

  sidebarHandler = () => {
    const { data } = this.props;
    const element = document.getElementById("main-wrapper");
    switch (data.settings[0].sidebartype) {
      case "full":
      case "iconbar":
        element.classList.toggle("mini-sidebar");
        if (element.classList.contains("mini-sidebar")) {
          element.setAttribute("data-sidebartype", "mini-sidebar");
        } else {
          element.setAttribute(
            "data-sidebartype",
            data.settings[0].sidebartype
          );
        }
        break;

      case "overlay":
      case "mini-sidebar":
        element.classList.toggle("full");
        if (element.classList.contains("full")) {
          element.setAttribute("data-sidebartype", "full");
        } else {
          element.setAttribute(
            "data-sidebartype",
            data.settings[0].sidebartype
          );
        }
        break;

      default:
    }
  };

  /*--------------------------------------------------------------------------------*/
  /* To open Search Bar                                                              */
  /*--------------------------------------------------------------------------------*/
  toggleMenu = () => {
    document.getElementById("search").classList.toggle("show-search");
  };

  render() {
    const { data } = this.props;
    const { isOpen } = this.state;
    const logoStyle = {
      cursor: "pointer",
      shadowBox: "2px 2px 2px black"
    };
    return (
      <header
        className="topbar navbarbg"
        data-navbarbg={data.settings[0].navbarbg}
      >
        <Navbar
          className={`top-navbar ${
            data.settings[0].navbarbg === "skin6"
              ? "navbar-light"
              : "navbar-dark"
          }`}
          expand="md"
        >
          <Collapse
            className="navbarbg m-0"
            isOpen={isOpen}
            navbar
            data-navbarbg={data.settings[0].navbarbg}
          >
            <Nav className="float-right" navbar>
              <NavItem>
                <NavLink
                  href="#"
                  className="d-none d-md-block"
                  onClick={this.sidebarHandler}
                >
                  <img
                    src={logolighttext}
                    style={lightTextStyles}
                    className="light-logo"
                    alt="homepage"
                  />
                </NavLink>
              </NavItem>
            </Nav>
          </Collapse>

          <div
            className="navbar-header"
            id="logobg"
            data-logobg={data.settings[0].logobg}
          >
            {/*--------------------------------------------------------------------------------*/}
            {/* Mobile View Toggler  [visible only after 768px screen]                         */}
            {/*--------------------------------------------------------------------------------*/}
            <a
              className="nav-toggler d-block d-md-none text-white"
              onClick={this.showMobilemenu}
              onKeyUp={this.showMobilemenu}
              role="button"
              tabIndex="0"
            >
              <img
                src={logolighttext}
                style={lightTextStyles}
                className="light-logo"
                alt="homepage"
              />
            </a>
          </div>
        </Navbar>
      </header>
    );
  }
}
Header.propTypes = {
  data: PropTypes.shape({
    theme: PropTypes.string,
    layout: PropTypes.string,
    dir: PropTypes.string,
    sidebartype: PropTypes.string,
    sidebarpos: PropTypes.string,
    headerpos: PropTypes.string,
    boxed: PropTypes.string,
    navbarbg: PropTypes.string,
    sidebarbg: PropTypes.string,
    logobg: PropTypes.string
  }).isRequired
};
export default Header;



