import React, { Component } from "react";
import PropTypes from "prop-types";
// import cardImage from "../../assets/images/cardRound.png";

export class CardComponent extends Component {
  static propTypes = {
    user: PropTypes.shape({
      nCard: PropTypes.string.isRequired,
      //   name: PropTypes.string.isRequired,
      valCard: PropTypes.string.isRequired
    }).isRequired
  };

  constructor(props) {
    super(props);
    this.state.nCard = props.user.nCard.replace(/-/gi, " ");
    this.state.valCard = props.user.valCard;
    // this.state.name = props.user.name;
  }

  state = {
    nCard: "",
    // name: "",
    valCard: ""
  };

  componentWillReceiveProps({ user }) {
    const { valCard: val, nCard: n } = user;
    this.setState({
      valCard: val,
      nCard: n.replace(/-/gi, " ")
    });
  }

  render() {
    const { nCard, valCard } = this.state;
    return (
      <div className="cardComponent m-auto">
        <div id="nCardLabel" className="label">
          NÚMERO
        </div>
        <div id="nCard">{nCard}</div>

        <div id="valCardLabel" className="label">
          VALID THRU
        </div>
        <div id="valCard">{valCard}</div>
      </div>
    );
  }
}

export default CardComponent;



