const validator = {
  cnpj: {
    rules: [
      {
        test: /^[0-9]{2}\.?[0-9]{3}\.?[0-9]{3}\/?[0-9]{4}\-?[0-9]{2}/,
        message: "Informe um CNPJ válido."
      }
    ],
    errors: [],
    valid: false,
    state: "",
    mask: "99.999.999/9999-99"
  },
  cpfResponsavel: {
    rules: [
      {
        test: /^[0-9]{3}.?[0-9]{3}.?[0-9]{3}-?[0-9]{2}/,
        message: "Informe um CPF válido."
      }
    ],
    errors: [],
    valid: false,
    state: "",
    mask: "999.999.999-99"
  },
  nomeFantasia: {
    rules: [
      {
        test: x => {
          return x && x.length > 3;
        },
        message: "Informe um nome com pelo menos três caractéres."
      }
    ],
    errors: [],
    valid: false,
    state: "",
    mask: ""
  },
  telefoneEmpresa: {
    rules: [
      {
        test: /^(\(*[1-9]{2}\)*)* *(?:[2-8]|9[1-9])[0-9]{3}\-*[0-9]{4}/,
        message: "Informe um telefone (com ddd)"
      }
    ],
    errors: [],
    valid: false,
    state: "",
    mask: "(99) 999999999"
  },
  nomeResponsavel: {
    rules: [
      {
        test: x => {
          return x && x.length > 3;
        },
        message: "Informe um nome com pelo menos três caractéres."
      }
    ],
    errors: [],
    valid: false,
    state: ""
  },
  cepEmpresa: {
    rules: [
      {
        test: /[0-9]{5}-*[0-9]{3}/,
        message: "Informe um cep válido."
      }
    ],
    errors: [],
    valid: false,
    state: "",
    mask: "99999-999"
  },
  complementoEmpresa: {
    rules: [
      {
        test: true,
        message: "Informe um complemento válido."
      }
    ],
    errors: [],
    valid: false,
    state: "",
    mask: ""
  },
  enderecoEmpresa: {
    rules: [
      {
        test: x => {
          return x && x.length > 3;
        },
        message: "Informe um endereço válido."
      }
    ],
    errors: [],
    valid: false,
    state: "",
    mask: ""
  },
  numeroEmpresa: {
    rules: [
      {
        test: /[_a-z0-9]{1,6}/,
        message: "Inválido."
      }
    ],
    errors: [],
    valid: false,
    state: "",
    mask: "******"
  },
  bairroEmpresa: {
    rules: [
      {
        test: x => {
          return x && x.length > 3;
        },
        message: "Informe um bairro válido."
      }
    ],
    errors: [],
    valid: false,
    state: "",
    mask: ""
  },
  cidadeEmpresa: {
    rules: [
      {
        test: x => {
          return x && x.length > 3;
        },
        message: "Informe uma cidade válida."
      }
    ],
    errors: [],
    valid: false,
    state: "",
    mask: ""
  },
  estadoEmpresa: {
    rules: [
      {
        test: x => {
          return x && x.length > 1;
        },
        message: "UF inválida."
      }
    ],
    errors: [],
    valid: false,
    state: "",
    mask: "aa"
  },
  email: {
    rules: [
      {
        test: /^[_a-z0-9-]+(\.[_a-z0-9-]+)*@[a-z0-9-]+(\.[a-z0-9-]+)*(\.[a-z]{2,})$/i,
        message: "Informe um e-mail válido."
      }
    ],
    errors: [],
    valid: false,
    state: "",
    mask: ""
  },
  password: {
    rules: [
      {
        test: x => {
          return x && x.length > 6;
        },
        message: "A senha precisa ser maior do que 6 caractéres."
      }
    ],
    errors: [],
    valid: false,
    state: "",
    mask: ""
  },
  passwordConfirmation: {
    rules: [
      {
        test: (x, y) => {
          return x === y;
        },
        message: "Confirme a senha."
      }
    ],
    errors: [],
    valid: false,
    state: "",
    mask: ""
  }
};

export default validator;



