import React from "react";
import Step0 from "./step-0";
import Step1 from "./step-1";
import Step2 from "./step-2";
import Step3 from "./step-3";
import StepDone from "./step-done";
import Steps from "../../components/steps-banky/steps-banky";
import img from "../../assets/images/logo-white.svg";
import { Row, Col, Container } from "reactstrap";
class SelfRegister extends React.Component {
  state = {
    step: 0,
    step1: {
      cnpj: "",
      nomeFantasia: "",
      telefoneEmpresa: "",
      nomeResponsavel: "",
      cpfResponsavel: ""
    },
    step2: {
      cepEmpresa: "",
      enderecoEmpresa: "",
      numeroEmpresa: "",
      complementoEmpresa: "",
      bairroEmpresa: "",
      cidadeEmpresa: "",
      estadoEmpresa: ""
    },
    step3: {
      email: "",
      password: "",
      passwordConfirmation: ""
    }
  };

  nextStep = () => {
    const { step } = this.state;
    this.setState({ step: step + 1 });
  };
  prevStep = () => {
    const { step } = this.state;
    this.setState({ step: step - 1 });
  };

  handleChanges = (input, value) => {
    const stepKey = "step" + this.state.step;
    let state = this.state[stepKey];
    state[input] = value;
    this.setState({
      [stepKey]: state
    });
  };

  renderStep(step, values) {
    switch (step) {
      case 0:
        return <Step0 nextStep={this.nextStep} />;
      case 1:
        return (
          <Step1
            nextStep={this.nextStep}
            handleChanges={this.handleChanges}
            values={values.step1}
          />
        );
      case 2:
        return (
          <Step2
            nextStep={this.nextStep}
            handleChanges={this.handleChanges}
            values={values.step2}
          />
        );
      case 3:
        return (
          <Step3
            nextStep={this.nextStep}
            handleChanges={this.handleChanges}
            values={values.step3}
          />
        );
      case 4:
        return <StepDone />;
      default:
        break;
    }
  }
  render() {
    const { step } = this.state;

    const {
      step1: {
        cnpj,
        nomeFantasia,
        telefoneEmpresa,
        nomeResponsavel,
        cpfResponsavel
      },
      step2: {
        cepEmpresa,
        enderecoEmpresa,
        numeroEmpresa,
        complementoEmpresa,
        bairroEmpresa,
        cidadeEmpresa,
        estadoEmpresa
      },
      step3: { email, password, passwordConfirmation }
    } = this.state;
    const values = {
      step1: {
        cnpj,
        nomeFantasia,
        telefoneEmpresa,
        nomeResponsavel,
        cpfResponsavel
      },
      step2: {
        cepEmpresa,
        enderecoEmpresa,
        numeroEmpresa,
        complementoEmpresa,
        bairroEmpresa,
        cidadeEmpresa,
        estadoEmpresa
      },
      step3: { email, password, passwordConfirmation }
    };
    return (
      <Container fluid className="self-register">
        {/*      */}
        {/* Logo */}
        {/*      */}
        <Row
          noGutters
          className="justify-content-center align-items-center logo-wrapper"
        >
          <Col xs="12" className="text-center">
            <img className="logo" src={img} alt="Logo Banky" />
          </Col>
        </Row>
        <Row noGutters className="justify-content-center">
          <Col className="stepsComponent">
            <Steps steps={step} maxSteps="3" prevStep={this.prevStep} />
          </Col>
        </Row>
        <div className="content-wrapper">{this.renderStep(step, values)}</div>
      </Container>
    );
  }
}

export default SelfRegister;



