import React, { Component } from "react";
import { Button, Row, Col, Form, Container } from "reactstrap";
import Validator from "./validator";
import InputBanky from "../../components/input-banky/input-banky";

export class queueForm extends Component {
  state = {
    cnpj: "",
    email: "",
    celular: "",
    url: "",
    nome: ""
  };

  constructor(props) {
    super(props);
    const { nextStep } = this.props;
    this.validators = Validator;
    this.nextStep = nextStep;
  }

  handleChange = e => {
    const change = {};
    change[e.target.name] = e.target.value;
    this.setState(change);
  };

  handleClick = e => {
    e.preventDefault();
    // const res = this.validForm();
    const res = true;
    if (res) this.nextStep();
  };

  validForm() {
    let status = true;
    Object.keys(this.validators).forEach(field => {
      const { [field]: currentState } = this.state;
      this.formValidators(field, currentState);
      if (!this.validators[field].valid) {
        status = false;
      }
    });
    return status;
  }

  showErrors(fieldName) {
    if (fieldName === "") return <div className="error" />;
    const validator = this.validators[fieldName];
    const result = "";

    if (validator && !validator.valid) {
      const errors = validator.errors.map((info, index) => (
        // eslint-disable-next-line react/no-array-index-key
        <span className="error" key={index}>
          <i className="mdi mdi-alert-octagram" /> {info}
          <br />
        </span>
      ));
      return <div className="error">{errors}</div>;
    }
    return result;
  }

  formValidators(fieldName, value) {
    this.validators[fieldName].errors = [];
    this.validators[fieldName].state = value;
    this.validators[fieldName].valid = true;
    this.validators[fieldName].rules.forEach(rule => {
      if (rule.test instanceof RegExp) {
        if (!rule.test.test(value)) {
          this.validators[fieldName].errors.push(rule.message);
          this.validators[fieldName].valid = false;
        }
      } else if (typeof rule.test === "function") {
        if (!rule.test(value)) {
          this.validators[fieldName].errors.push(rule.message);
          this.validators[fieldName].valid = false;
        }
      }
    });
    this.setState(st => this.setState(st));
  }

  render() {
    const { cnpj, email, celular, url, nome } = this.state;
    return (
      <Container fluid className="QueueForm">
        {/*              */}
        {/* Introduction */}
        {/*              */}
        <Row noGutters className="my-3 justify-content-center">
          <Col className="text-center introduction">
            Chega de misturar as contas pessoais com contas corporativas!
            Preencha os dados abaixo e entre em nossa lista de espera.
          </Col>
        </Row>
        {/*      */}
        {/* Form */}
        {/*      */}
        <Form className="form-material">
          <Row className="justify-content-center align-content-top">
            <Col xs="12" className="mx-auto">
              <InputBanky
                label="CNPJ"
                type="text"
                onChange={this.handleChange}
                mask={this.validators.cnpj.mask}
                value={cnpj}
                name="cnpj"
                formNoValidate
                required
              />
              {this.showErrors("cnpj")}
            </Col>

            <Col xs="12" className="mx-auto">
              <InputBanky
                label="E-mail"
                mask={this.validators.email.mask}
                onChange={this.handleChange}
                type="email"
                value={email}
                name="email"
                formNoValidate
                required
              />
              {this.showErrors("email")}
            </Col>

            <Col xs="12" lg="5" className="mx-auto">
              <InputBanky
                label="Celular"
                type="tel"
                mask={this.validators.celular.mask}
                onChange={this.handleChange}
                value={celular}
                name="celular"
                formNoValidate
                required
              />
              {this.showErrors("celular")}
            </Col>
            <Col xs="12" lg="7" className="mx-auto">
              <InputBanky
                label="Site (Opcional)"
                type="url"
                mask={this.validators.url.mask}
                onChange={this.handleChange}
                value={url}
                formNoValidate
                name="url"
              />
              {this.showErrors("url")}
            </Col>

            <Col xs="12" className="mx-auto">
              <InputBanky
                label="Nome para contato"
                type="text"
                mask={this.validators.nome.mask}
                onChange={this.handleChange}
                value={nome}
                formNoValidate
                name="nome"
              />
              {this.showErrors("nome")}
            </Col>

            <Col xs="12" className="mx-auto">
              <Button
                size="lg"
                block
                type="submit"
                onClick={this.handleClick}
                className="btn btn-light text-primary"
              >
                Entrar na lista de espera
              </Button>
            </Col>
          </Row>
        </Form>
      </Container>
    );
  }
}

export default queueForm;



