import React, { Component } from "react";
export default class StepsBanky extends Component {
  constructor(props) {
    super(props);
    this.state = { steps: props.steps, maxSteps: props.maxSteps };
  }

  drawElements() {
    let line = [];

    for (let i = 1; i <= this.state.maxSteps; i++) {
      line.push(this.drawState(i));
      if (i < this.state.maxSteps) line.push(this.drawLine(i));
    }
    return line;
  }
  drawState(number) {
    if (number < this.state.steps) return this.drawPrevious(number);
    if (number == this.state.steps) return this.drawCurrent(number);
    if (number > this.state.steps) return this.drawNext(number);
  }

  drawPrevious(number) {
    return (
      <div className="component" key={number.toString()}>
        <span className="circle-wrapper" onClick={this.goBack(number)}>
          <svg
            xmlns="http://www.w3.org/2000/svg"
            version="1.1"
            className="circle-active"
            focusable="false"
            viewBox="0 0 10 10"
            aria-hidden="true"
            role="presentation"
          >
            <circle className="circle" cx="5" cy="5" r="3" />
            <text className="number-label" x="5" y="6.5" textAnchor="middle">
              {number}
            </text>
          </svg>
        </span>
      </div>
    );
  }

  drawCurrent(number) {
    return (
      <div className="component" key={number.toString()}>
        <span className="circle-wrapper">
          <svg
            xmlns="http://www.w3.org/2000/svg"
            version="1.1"
            className="circle-current"
            focusable="false"
            viewBox="0 0 10 10"
            aria-hidden="true"
            role="presentation"
          >
            <circle className="circle" cx="5" cy="5" r="3" />
            <text className="number-label" x="5" y="6.5" textAnchor="middle">
              {number}
            </text>
          </svg>
        </span>
      </div>
    );
  }

  drawNext(number) {
    return (
      <div className="component" key={number.toString()}>
        <span className="circle-wrapper">
          <svg
            xmlns="http://www.w3.org/2000/svg"
            version="1.1"
            className="circle-inactive"
            focusable="false"
            viewBox="0 0 10 10"
            aria-hidden="true"
            role="presentation"
          >
            <circle className="circle" cx="5" cy="5" r="3" />
            <text className="number-label" x="5" y="6.5" textAnchor="middle">
              {number}
            </text>
          </svg>
        </span>
      </div>
    );
  }

  drawLine(number) {
    return (
      <div key={number + 0.5} className="line-wrapper">
        <span className="line" />
      </div>
    );
  }
  componentWillReceiveProps({ steps }) {
    this.setState({ ...this.state, steps });
  }
  goBack = number => e => {
    if (number <= this.state.maxSteps) this.props.prevStep(number);
  };
  render() {
    const { steps } = this.state;
    return <div className="steps-banky">{this.drawElements()}</div>;
  }
}



