import React from "react";
import { Row, Col, Container } from "reactstrap";
import QueueForm from "./queue-form";
import QueueDone from "./queue-done";
import img from "../../assets/images/logo-white.svg";

class Queue extends React.Component {
  state = {
    step: 1
  };

  nextStep = () => {
    const { step } = this.state;
    const s = 1 - step;
    this.setState({ step: s });
  };

  renderStep(step) {
    switch (step) {
      case 0:
        return <QueueForm nextStep={this.nextStep} />;
      case 1:
        return <QueueDone nextStep={this.nextStep} />;
      default:
        return <QueueForm nextStep={this.nextStep} />;
    }
  }

  render() {
    const { step } = this.state;
    return (
      <Container fluid className="Queue">
        {/*      */}
        {/* Logo */}
        {/*      */}
        <Row
          noGutters
          className="justify-content-center align-items-center logo-wrapper"
        >
          <Col xs="12" className="text-center">
            <img className="logo" src={img} alt="Logo Banky" />
          </Col>
        </Row>
        <div className="content-wrapper">{this.renderStep(step)}</div>
      </Container>
    );
  }
}

export default Queue;



