const validator = {
  email: {
    rules: [
      {
        test: /^[_a-z0-9-]+(\.[_a-z0-9-]+)*@[a-z0-9-]+(\.[a-z0-9-]+)*(\.[a-z]{2,})$/i,
        message: "Informe um e-mail válido."
      }
    ],
    errors: [],
    valid: false,
    state: "",
    mask: ""
  },
  cnpj: {
    rules: [
      {
        test: /^[0-9]{2}\.?[0-9]{3}\.?[0-9]{3}\/?[0-9]{4}\-?[0-9]{2}/,
        message: "Informe um CNPJ válido."
      }
    ],
    errors: [],
    valid: false,
    state: "",
    mask: "99.999.999/9999-99"
  },
  celular: {
    rules: [
      {
        test: /^(\(*[1-9]{2}\)*)* *(?:[2-8]|9[1-9])[0-9]{3}\-*[0-9]{4}/,
        message: "Informe um telefone (com ddd)"
      }
    ],
    errors: [],
    valid: false,
    state: "",
    mask: "(99) 999999999"
  },
  url: {
    rules: [
      {
        test: /^(?:http(s)?:\/\/)?[\w.-]+(?:\.[\w\.-]+)+[\w\-\._~:/?#[\]@!\$&'\(\)\*\+,;=.]+$/,
        message: "Informe uma url válida"
      }
    ],
    errors: [],
    valid: false,
    state: "",
    mask: ""
  },
  nome: {
    rules: [
      {
        test: x => x && x.length > 3,
        message: "Informe um nome com pelo menos três caractéres."
      }
    ],
    errors: [],
    valid: false,
    state: ""
  }
};

export default validator;



